import React, {useState } from 'react'

import './styles.scss';
import {Utils} from '../../helper'



function EditBody(props){
  return <div className="pdf__edit__options">{props.children}</div>
}

function PageMarginSize(props){

  const handleMargin = event => {
    let isProcess = props?.data?.isProcess??false;
    if(!!isProcess) return;

    let value = event.target.value;
    value = Utils.inputNumber(value,0);
    value = value >= 100?100:value;
    if(props.handler){
      props.handler({margin:value});
    }
  }

  let margin = props?.data?.margin??10;

  return(
    <div className="pdf__edit__option">
      <p className="pdf__edit__option__title"><span>Margin Size</span>:</p>
      <div className="pdf__edit__option__input">
        <input type="number" value={margin} onChange={handleMargin}/>
        <span>px</span>
      </div>
    </div>
  )
}


function SizeSelector(props){
  const options = [
    {name:"A4",value:"a4"},
    {name:"A5",value:"a5"},
    {name:"A6",value:"a6"},
    {name:"4 x 6",value:"4x6"},
    {name:"5 x 8",value:"5x8"},
    {name:"8 x 10",value:"8x10"}
  ]
  let isProcess = props?.data?.isProcess??false;
  let selected = props?.data?.size??"a4";
  let _name = options.find(i=>i.value == selected)?.name;


  const [isActive, setActive] = useState(false);
  const handleActive = () =>{
    if(!!isProcess) return;
    setActive(!isActive);
  }
  const handleSelect = option => {
    if(!!isProcess || selected === option) return;
    if(props.handler){
      props.handler({size:option});
    }
  }

  const renderOptions = () =>{
    return options.map(i=>{
      return <div className="select-option" onClick={()=>handleSelect(i.value)} key={i.value}>{i.name}</div>
    })
  }


  return(
    <div className="pdf__edit__option">
      <p className="pdf__edit__option__title"><span>Page Size</span>:</p>
      <div className="pdf__edit__option__select">
      <div className={`select${isActive?" active":""}`} onClick={handleActive}>
        <div className="select-head">
          <div className="select-selected">{_name}</div>
          <div className="select-toggle"><svg width="512" height="298" viewBox="0 0 512 298.04"><path d="M12.08 70.78c-16.17-16.24-16.09-42.54.15-58.7 16.25-16.17 42.54-16.09 58.71.15L256 197.76 441.06 12.23c16.17-16.24 42.46-16.32 58.71-.15 16.24 16.16 16.32 42.46.15 58.7L285.27 285.96c-16.24 16.17-42.54 16.09-58.7-.15L12.08 70.78z"/></svg></div>
        </div>
        {isActive && <div className="select-options">
          {renderOptions()}
        </div>}
      </div>
      </div>
    </div>
  )
}
