import React, {Fragment } from 'react'

import './styles.scss';
import {ReactComponent as ImageIcon} from '../../images/image.svg';
import {ReactComponent as CrossIcon} from '../../images/cross.svg';


function SelectImage(props) {

  const handleFile = event =>{
    let file = event.target.files[0];
    if(!file) return;
    props.handler(file);
  }

  return <div className="file__upload__wrap">
    <input id="file-input" type='file' accept="image/*" onChange={handleFile}/>
    <label htmlFor="file-input">
    <ImageIcon/>
      <p>Select an image</p>
    </label>
  </div>
}

function SelectImages(props) {

  const handleFile = async event =>{
    try {
      let files = event.target.files;
      if(!files || !files.length) return;

      props.handler({isProcess:false});

      files = Array.from(files).map(async file=>{
        return await new Promise(resolve=>{
         let img  = new Image();
         let _url = URL.createObjectURL(file);
         img.onload = function () {
           resolve({url:_url,width:this.width,height:this.height});
         }
         img.src = _url;
        })
      });

      files = await Promise.all(files);
      files = files.filter(i=>!!i);
      props.handler({files,isProcess:false});
    } catch (e) {
      props.handler({isProcess:false});
      // console.log(e);
    }
  }

  return <div className="file__upload__wrap">
    <input id="file-input" type='file' accept="image/*" multiple="multiple" onChange={handleFile}/>
    <label htmlFor="file-input">
    <ImageIcon/>
      <p>Select images</p>
    </label>
  </div>
}

function DisplayImages(props) {

  let{files,isProcess} = props?.data??{};


  const handleFiles = async event =>{
    try {
      if(!!isProcess) return;
      let _files = event.target.files;
      if(!_files || !_files.length) return;
      props.handler({isProcess:true});

      _files = Array.from(_files).map(async file=>{
        return await new Promise(resolve=>{
         let img  = new Image();
         let _url = URL.createObjectURL(file);
         img.onload = function () {
           resolve({url:_url,width:this.width,height:this.height});
         }
         img.src = _url;
        })
      });

      _files = await Promise.all(_files);
      _files = _files.filter(i=>!!i);

      files = [...files,..._files]
      props.handler({files,isProcess:false});
    } catch (e) {
      props.handler({isProcess:false});
    }
  }

  const handleRemove = (index) =>{
    if(!!isProcess) return;
    files[index] = "";
    files = files.filter(i=>!!i);
    props.handler({files});
  }

  const renderFiles = () =>{
    return files.map((file,i)=>{
      return <div className="multi__files__display__each" key={i}>
        <img src={file.url} />
        <span onClick={()=>handleRemove(i)}><CrossIcon/></span>
      </div>
    })
  }

  return <div className="multi__files__display">
    {renderFiles()}
    {!isProcess && <div className="multi__files__display__each">
      <input id="add-file" type='file' accept="image/*" multiple="multiple" onChange={handleFiles}/>
      <label htmlFor="add-file"><CrossIcon/></label>
    </div>}
  </div>
}


export {SelectImage,SelectImages,DisplayImages}
