import React, { Component,Fragment } from 'react'
import { jsPDF } from "jspdf";

import './style.scss';
import {FilePicker,FilePreview,PdfOptions} from '../../components'
import DummyImage from '../../images/adiyogi.webp';


class ImagePdf extends Component{
  constructor(props) {
    super(props)
    this.unmounted = false;

    this.blank = {
      files:[],
      size:"a4",
      orientation:"portrait",
      fit:"contain",
      marginType:"normal",
      margin:10,
      compression:"SLOW",
      isProcess:false,
    }
    this.state = this.blank
  }

  setState(state, callback) {
    if (!this.unmounted) {
        super.setState(state, callback);
    }
  }

  componentDidMount(){
    document.title = "Image to Pdf convertor";

    // let _self = this;
    // fetch(DummyImage).then(res=>{
    //   res.blob().then((blob) => {
    //     const file = new File([blob], 'adiyogi.webp', {type: blob.type});
    //     _self.setState({files:[{url:URL.createObjectURL(file),width:1024,height:828}]})
    //   })
    // })
  }

  componentWillUnmount(){
    this.unmounted = true;
    document.title = "Editor | Home";
  }



   handleDownload = async() =>{
     try {
      let{files,size,orientation,fit,marginType,margin,compression,isProcess} = this.state;
      if(!!isProcess) return;
      this.setState({isProcess:true});
      await new Promise((resolve)=>setTimeout(resolve, 1));

      margin = margin || 0;

      if(size === "4x6"){
        size = [288,432];
      }else if (size === "5x8") {
        size = [360,576];
      }else if (size === "8x10") {
        size = [576,720];
      }

      let options = {
        orientation:orientation,
        format:size,
        unit:"px",
        compress:true,
      }

      const pdf = new jsPDF(options);
      let width = pdf.internal.pageSize.getWidth();
      let height = pdf.internal.pageSize.getHeight();


      function insertNew(i=0){

        if(i === files.length) {
          pdf.save("download.pdf");
          return;
        }

        let file = files[i];

        let _left = margin;
        let _top = margin;
        let _width = width - 2*margin;
        let _height = height - 2*margin;

        let _w = file.width;
        let _h = file.height;

        if(fit === "normal"){
          _width = _w;
          _height = _h;
        }else if(fit === "contain"){
          _h = _h*_width/_w;
          _w = _width;
          if(_h > _height){
            _w = _w*_height/_h;
            _h = _height;
          }
          _width = _w;
          _height = _h;
        }

        if(marginType === "uniform"){
          _left = (width-_width)/2;
          _left = fit === "normal" && _left < margin?margin:_left;
          _top = (height-_height)/2;
          _top = fit === "normal" && _top < margin?margin:_top;
        }

        let _page = i+1;
        if(_page>1){
          pdf.addPage();
        }
        pdf.addImage(file.url, 'JPEG', _left,_top,_width,_height,_page,compression);
        insertNew(_page)
      }
      insertNew()


      this.setState({isProcess:false});

     } catch (e) {
       this.setState({isProcess:false});
     }
  }


  render(){
    let{files,size,orientation,fit,marginType,margin,compression,isProcess} = this.state;
    return(
      <div className="section__model section__model_row main__margin">
        {!files.length && <FilePicker.SelectImages handler={(val)=>this.setState(val)}/>}

        {!!files.length && <Fragment>
          <div className="section__model_half">
            <FilePicker.DisplayImages
              data={{files,isProcess}}
              handler={(val)=>this.setState(val)}
            />
          </div>
          <div className="section__model_half">

            <PdfOptions.EditBody>
              <PdfOptions.SizeSelector data={{size,isProcess}} handler={(val)=>this.setState(val)} />
              <PdfOptions.OrientationSelector data={{orientation,isProcess}} handler={(val)=>this.setState(val)} />
              <PdfOptions.FittingSelector data={{fit,isProcess}} handler={(val)=>this.setState(val)} />
              <PdfOptions.PageMargin data={{marginType,isProcess}} handler={(val)=>this.setState(val)} />
              <PdfOptions.PageMarginSize data={{margin,isProcess}} handler={(val)=>this.setState(val)} />
              <PdfOptions.CompressSelector data={{compression,isProcess}} handler={(val)=>this.setState(val)} />

            </PdfOptions.EditBody>


          <button className="download__btn" onClick={this.handleDownload}>
            Convert PDF
            {!!isProcess && <div className="ball__pulse"><div></div><div></div><div></div></div>}
          </button>

          </div>
        </Fragment>}

      </div>
    )
  }
}

export default ImagePdf;
